      b-none Entre los desinfectantes podremos encontrar a 
      b(style="color: #008F85;") los biocidas que son sustancias conformadas por molculas qumicas que por medios qumico o biolgico puede destruir, contrarrestar, neutralizar, impedir la accin o ejercer un efecto de control sobre cualquier organismo nocivo. 
      b-none El mecanismo de accin de un biocida se basa en la alteracin de la estructura del microorganismo, como lo puede ser en la entrada o salida de sustancias fundamentales, lo cual provoca la destruccin del agente microbiano. 
      b Los principales mecanismos de accin de los agentes desinfectantes son: 

