          .row(numero="1" titulo="Agentes oxidantes")
            .col-md-12.mb-4.mb-md-0
              p.mb-4 Su poder germicida se debe principalmente a su capacidad de ceder oxgeno, llevando a la inactivacin de ciertas protenas enzimticas. Los principales agentes oxidantes son:
              
              .tarjeta-avatar-b.mb-2
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/tema2/icon11.svg' alt='AvatarTop')
                .tarjeta.bg-line2(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                  .p-4
                    h4.mb-2 Agua oxigenada
                    p.mb-0 Es una solucin de perxido de hidrgeno fue empleada como desinfectante durante mucho tiempo, pero actualmente ha cado en desuso, esto debido a que puede ser descompuesta por algunas bacterias resistentes de accin catalasa y peroxidasa. 
                    
              .tarjeta-avatar-b.mb-2
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/tema2/icon11.svg' alt='AvatarTop')
                .tarjeta.bg-line3(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                  .p-4
                    h4.mb-2 Permanganato potsico
                    p.mb-0 Se utiliza para desinfectar heridas, es de accin lenta y puede ser inactivado con facilidad, por ello es poco empleado. 
                    
          .row(numero="2" titulo="cidos")
            .col-md-12.mb-4.mb-md-0
              p.mb-4 Sustancia qumica que emite iones de hidrgeno en el agua y forma sales cuando se combina con ciertos metales.  Entre ellos encontramos: 
              .row.justify-content-center
                .col-auto.mb-3
                  figure
                    img.mx-auto(src='@/assets/curso/tema2/img8.svg' alt='AvatarTop' style='width:250px;')
                .col-auto.mb-3
                  figure
                    img.mx-auto(src='@/assets/curso/tema2/img9.svg' alt='AvatarTop' style='width:250px;')
                .col-auto.mb-3
