          .row(numero="3" titulo="Alcoholes")
            .col-md-12.mb-4.mb-md-0
              p.mb-4 Esta sustancia posee propiedades germicidas gracias a su capacidad de desnaturalizar las protenas plasmticas y la disminucin de la tensin interfacial, aunque esta potencia antisptica es variable. Los alcoholes alifticos son bactericidas de potencia intermedia y son eficaces para la mayora de las bacterias existentes en la piel, aunque no destruyen las esporas. Entre ellos encontramos:
              .tarjeta-avatar-b.mb-2
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/tema2/icon12.svg' alt='AvatarTop')
                .tarjeta.bg-line2(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                  .p-4
                    h4.mb-2 El alcohol etlico o etanol 
                    p.mb-0 Se emplea tpicamente sobre la piel como antisptico a una concentracin del 70 % p/v (a 100 % de pureza es poco efectivo). Se emplea en desinfeccin de instrumentos quirrgicos, siempre que se deje el tiempo suficiente de contacto. 
                
              .tarjeta-avatar-b.mb-4
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/tema2/icon12.svg' alt='AvatarTop')
                .tarjeta.bg-line3(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                  .p-4
                    h4.mb-2 Isopropanol
                    p.mb-0 este alcohol cuenta con un poder desinfectante superior al del etanol y es menos voltil y corrosivo sobre los objetos metlicos. Por el contrario, resulta ms irritante que el anterior. Se emplea principalmente en la limpieza y desinfeccin. 
              
              p.mb-4 En general los alcoholes no son recomendados para la esterilizacin de instrumentos quirrgicos debido principalmente a que es incapaz de daar a microorganismos esporulados y no puede penetrar materiales ricos en protenas. 
          
          .row(numero="4" titulo="Aldehdos (formol)")
            .col-md-12.mb-4.mb-md-0 
              
              .row.justify-content-center
                .col-auto.align-self-center.mb-4
                  figure
                    img.mx-auto(src='@/assets/curso/tema2/img11.png' alt='AvatarTop' style='width:186px;')
                .col.mb-4.align-self-center
                  p.mb-0 El formaldehdo es un potente desinfectante, tiene un gran poder de penetracin y de accin irritante para la piel y mucosas. Es txico, en su forma gaseosa, ya que produce irritacin de la mucosa ocular y respiratoria; como por ingestin, produciendo alteraciones digestivas y del sistema nervioso. Suele utilizarse para la desinfeccin de instrumentos y superficies inertes, y es la principal sustancia qumica para la realizacin del proceso de embalsamamiento.
                
          .row(numero="5" titulo="Glutaraldehdo")
            .col-md-12.mb-4.mb-md-0 
              
              .row.justify-content-center
                .col-auto.align-self-center.mb-4
                  figure
                    img.mx-auto(src='@/assets/curso/tema2/img12.png' alt='AvatarTop' style='width:220px;')
                .col.mb-4.align-self-center
                  p.mb-0 Es un dialdehdo saturado utilizado como desinfectante de alto nivel. Su dilucin principal es cida y en ese estado sus propiedades microbicidas son menores, por esto es activada (alcalinizada) mediante el uso de agentes que elevan el pH de la solucin (7.5 -8.5) Es en este momento cuando la solucin alcanza su mximo poder, convirtindose en un excelente bactericida, fungicida y virucida. Por esto se recomienda el uso de este qumico desinfectante en el laboratorio de tanatopraxia.
              
          .row(numero="6" titulo="Halgenos y compuestos halogenados ")
            .col-md-12.mb-4.mb-md-0 
              
              .row.justify-content-center
                .col-auto.align-self-center.mb-4
                  figure
                    img.mx-auto(src='@/assets/curso/tema2/img13.png' alt='AvatarTop' style='width:186px;')
                .col.mb-4.align-self-center
                  p.mb-4 El grupo de los halgenos es el grupo 7 de la tabla peridica, y est formado por los siguientes elementos: flor, cloro, bromo, yodo y stato (este ltimo, radioactivo y poco comn) La palabra Halgeno proviene del griego y significa formador de sales. 
                  p.mb-0 Como el cloro que se usa principalmente para la desinfeccin del agua y el yodo como desinfectante de la piel, son dos bactericidas muy potentes que se utilizan como bactericidas y antispticos. 
          
          .row(numero="7" titulo="Cloro")
            .col-md-12.mb-4.mb-md-0 
              p.mb-4 Esta sustancia puede encontrarse bajo las formas de cloro gaseoso, hipocloritos y cloraminas. Es un potente germicida de accin rpida contra bacterias y virus, aunque su actividad disminuye al entrar en contacto con materia orgnica y al aumentar el pH. En cualquiera de sus presentaciones el mecanismo de accin desinfectante se debe a la liberacin de cloro libre que a su vez, junto al agua y en medio cido o neutro, origina cido hipocloroso. 
              .row.justify-content-center
                .col-auto.mb-2
                  .tarjeta-avatar-b.mb-0
                    .tarjeta-avatar-b__img
                      img(src='@/assets/curso/tema2/icon13.svg' alt='AvatarTop')
                    .tarjeta.bg-line2(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                      .p-4
                        h4.mb-2 Hipoclorito sdico
                        p.mb-0 Es una solucin muy potente como desinfectante para uso externo (ataca a virus y bacterias). Se emplea comnmente en desinfeccin de paredes, pisos, sanitarios, recipientes de alimentos, tiles de aseo, etc. 
                .col-auto.mb-2
                  .tarjeta-avatar-b.mb-0
                    .tarjeta-avatar-b__img
                      img(src='@/assets/curso/tema2/icon13.svg' alt='AvatarTop')
                    .tarjeta.bg-line3(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                      .p-4
                        h4.mb-2 Cloraminas
                        p.mb-0 Son derivados orgnicos del cloro, de actividad algo inferior al hipoclorito sdico, pero con menos poder irritante sobre la piel, ya que liberan el cloro de forma gradual. 

          
          .row(numero="8" titulo="Detergentes")
            .col-md-12.mb-4.mb-md-0 
              .row.justify-content-center
                .col.mb-4.align-self-center
                  p.mb-0 Es una sustancia que posee la propiedad de dispersar un slido como lo puede ser la suciedad o materia orgnica en el agua u otro lquido. Estos tambin podran llamarse tensioactivos y se clasifican en tres grupos: aninicos, catinicos y no inicos. Basndonos en la actividad antisptica el grupo de los catinicos es el ms importante.
                .col-auto.align-self-center.mb-4
                  figure
                    img.mx-auto(src='@/assets/curso/tema2/img14.png' alt='AvatarTop' style='width:258px;')
              p.mb-4 Los tensioactivos catinicos son compuestos de amonio cuaternario que cuentan con una accin potente y rpida. El espectro de actividad de estos productos es bastante elevado frente a bacterias y hongos, pero escaso frente a virus y esporas. En este grupo encontramos al cloruro de benzalconio y  que el cetrimide. 
              
          .row(numero="9" titulo="Fenoles y derivados")
            .col-md-12.mb-4.mb-md-0 
              p.mb-4 Son antispticos que actan desnaturalizando las protenas cuando se encuentran a concentraciones bajas. La potencia del este se incrementa a medida que aumenta el nmero de sustituyentes y la longitud de sus molculas. Este fue uno de los primeros antispticos usados, pero actualmente es poco utilizado por su gran toxicidad y su poder irritante, por ello se limita su empleo como desinfectante de uso limitado. Posee gran nmero de derivados como lo son:
              .row.justify-content-center
                .col-auto.mb-3
                  .tarjeta-avatar-b.mb-0
                    .tarjeta-avatar-b__img
                      img(src='@/assets/curso/tema2/icon14.svg' alt='AvatarTop')
                    .tarjeta.bg-line1(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                      .p-4
                        h4.mb-2 Cloroxinelol
                        p.mb-0 Acta contra la gran mayora de los microorganismos, pero su potencia se reduce en presencia de sangre o materia orgnica. 
                      
                .col-auto.mb-3
                  .tarjeta-avatar-b.mb-0
                    .tarjeta-avatar-b__img
                      img(src='@/assets/curso/tema2/icon14.svg' alt='AvatarTop')
                    .tarjeta.bg-line2(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                      .p-4
                        h4.mb-2 Hexaclorofeno
                        p.mb-0 Es un bacteriosttico activo que acta las bacterias frente a grampositivas, pero resulta menos eficaz en gramnegativas y no acta frente a las esporas.
                      
                .col-auto.mb-3
                  .tarjeta-avatar-b.mb-0
                    .tarjeta-avatar-b__img
                      img(src='@/assets/curso/tema2/icon14.svg' alt='AvatarTop')
                    .tarjeta.bg-line3(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                      .p-4
                        h4.mb-2 Cresol
                        p.mb-0 Es una mezcla de los tres ismeros (orto, meta y para cresoles) es altamente irritante por ello solo se emplea en desinfeccin exterior y solucin jabonosa.
                      
                .col-auto.mb-3
                  .tarjeta-avatar-b.mb-0
