                    .tarjeta.bg-line4(style="background-color: rgb(255, 255, 255); align-self: center; border-radius:8px;")
                      .p-4
                        h4.mb-2 Triclosn
                        p.mb-0 Es eficaz frente a bacterias gram negativas y positivas y se utiliza como antisptico en presentacin de jabones y desodorantes.
                      

    p.mb-5 Luego de hablar sobre los qumicos desinfectantes ms usados para la desinfeccin de reas, insumos, elementos y dems; 
      b  los desinfectantes recomendados en la labor de 
      b(style="color: #008F85;") tanatopraxia 
      b son los siguientes:
    
    .row.justify-content-center.mb-2.fondo-bg6
