                    .px-3
                      .bg-num1
                          h4.mb-2 Yodoforos
                          p.pb-5 En una dilucin de 1 % durante al menos 10 minutos o con una concentracin entre el 75-200 ppm y realizar la dilucin segn el manual del fabricante. Ejemplo: betadine, wescodyne, HiSine y losan.
          
          .col-auto.mb-5.px-2
            .tarjeta.tarjeta-container-Larga(style='height:470px; box-shadow: 0 0 15px rgba(0,0,0,.3)')
              .tarjeta-larga1
                .tarjetaLarga-basic1(style='height:470px;')
                  .tarjetaImgL(style='height:470px;')
