                    .px-3
                      .bg-num2
                          h4.mb-2 Fenoles y sus derivados
                          p.pb-5 Con una concentracin entre el 0.5- 3 % y usarse al menos durante 10 minutos y seguir las indicaciones de manufactura. No estn recomendados para uso en instrumentos o superficies duras, tal como la mesa de preparacin. Ejemplo: Amphyl y Staphene.
                
          .col-auto.mb-5.px-2
            .tarjeta.tarjeta-container-Larga(style='height:470px; box-shadow: 0 0 15px rgba(0,0,0,.3)')
              .tarjeta-larga2
                .tarjetaLarga-basic1(style='height:470px;')
                  .tarjetaImgL(style='height:470px;')
                    figure.mb-3
