                    .px-3
                      .bg-num3
                          h4.mb-2 Hipoclorito de sodio
                          p.pb-5 Concentracin de uso entre 1000 a 5000 ppm con agua del grifo, usarse por un periodo de 10 a 30 minutos, con especial cuidado ya que es altamente corrosivo.
                          
          .col-auto.mb-5.px-2
            .tarjeta.tarjeta-container-Larga(style='height:470px; box-shadow: 0 0 15px rgba(0,0,0,.3)')
              .tarjeta-larga1
