        .row.justify-content-center.mb-5
          p.mb-2 Tambin se debe de resaltar que todo uso de 
            b(style="color: #008F85;")  agentes qumicos 
            b  desinfectantes puede traer consigo diferentes problemas para la salud 
            b-none porque existen innumerables factores que influyen en este efecto. Empezando por:  
        
