                p.mb-0 Liberacin de rocos, vapores o gases que emanan algunos de los desinfectantes los cuales pueden irritar los ojos, la nariz, la garganta y los pulmones, llegando a causar asma o desencadenar ataques de asma.
            
        .row.justify-content-center.mb-5
          .cajon.color-secundario.p-4.mb-0
            p.mb-0 Las 
              b(style="color: #008F85;")  prcticas seguras de trabajo 
              b-none  al momento de usar los 
              b productos qumicos de limpieza y desinfeccin garantizan la disminucin de los peligros de contaminacin, 
              b-none lo cual conlleva a la 
              b conservacin de la integridad del personal directamente involucrado, sus compaeros de trabajo y su entorno. 
              b-none Para cumplir con lo anterior se sugieren las siguientes acciones:
        
        .row.justify-content-center.mb-4.mb-lg-5
          .col-lg-7.mb-2.align-self-center
            ul.lista-ul.ms-md-4
              li 
                i.fas.fa-biohazard
                p.mb-0 Controlar la fecha de vencimiento de los desinfectantes.
              li 
                i.fas.fa-biohazard
                p.mb-0 Abstenerse de mantener los objetos o instrumentos como pinzas, tijeras u otros sumergidos en soluciones desinfectantes por el riesgo de contaminacin y evaporacin de la solucin. 
              li 
                i.fas.fa-biohazard
                p.mb-0 Limpiar previamente y secar los recipientes donde se preparan las soluciones desinfectantes. 
              li 
                i.fas.fa-biohazard
                p.mb-0 Manipular con las manos limpias los desinfectantes.
              li 
                i.fas.fa-biohazard
                p.mb-0 Mantener los envases cerrados de los productos. 
              li 
                i.fas.fa-biohazard
                p.mb-0 No usar las soluciones de cloro pasadas las 12 horas posterior a la preparacin de la solucin, su efectividad disminuye con el paso de las horas y la evaporacin.
              li 
                i.fas.fa-biohazard
                p.mb-0 Aplicar los productos en superficies u objetos previamente descontaminados (limpios) para aumentar su efectividad. 
              li 
                i.fas.fa-biohazard
                p.mb-0 Usar de diluciones recomendadas por el fabricante y de los procedimientos autorizados en el lugar de trabajo. 
              li 
                i.fas.fa-biohazard
                p.mb-0 Uso de elementos de proteccin personal, como: guantes y mascarilla al momento de la dilucin de los desinfectantes. 
              li 
                i.fas.fa-biohazard
                p.mb-0 Uso de los productos sobre superficies y material inanimado.
          .col-8.col-lg-5.mb-0.mb-lg-2
