    separador
    #t_2_1.titulo-segundo
      .h2 2.1 Proceso de desinfeccin del cadver
    
    .bloque-texto-a.color-primario.p-4.p-md-5.mb-5 
      .row.m-0.align-items-center.justify-content-between
        .col-lg-9
          .bloque-texto-a__texto.p-5
            p.mb-0 Como se ha mencionado antes, 
              b la desinfeccin de un cadver comienza con unas tcnicas orientadas a la reduccin o eliminacin de microorganismos patgenos 
              b-none los cuales pueden alterar o 
              b(style="color: #008F85;") acelerar el proceso de descomposicin del cuerpo. 
              b-none Cuando se hace una
              b  desinfeccin interna del cadver se utilizan germicidas que se incorporan a los lquidos arteriales, de co-inyeccin y lquidos de cavidad.
              b-none  Con la intencin de 
              b(style="color: #008F85;") caracterizar las acciones a realizar cuando desinfecte un cuerpo fallecido, 
              b-none  lo invitamos a observar el siguiente recurso educativo. 
        .col-lg-3.mb-4.mb-lg-0
          figure
            img.mx-auto(src='@/assets/curso/tema2/img26.svg', alt='iamgen ambiental', style="width: 304px;")

    figure.mb-5
      .video
        <iframe width="853" height="480" src="https://www.youtube.com/embed/wtTeMZ4vzbQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      
    p.mb-4 Para concluir, se determina que los 
      b desinfectantes tpicos se utilizan para realizar la higienizacin 
      b-none de la piel, cabello, orificios externos del cadver y en algunos casos los tejidos expuestos como resultados de autopsias, traumas o donacin de rganos. 
      b(style="color: #008F85;") No olvide realizar la desinfeccin de bolsas, dispositivos mdicos, instrumental, mesas para embalsamar y dems equipos. 
    
    

    separador
    .row.justify-content-center.fondo-bg5
      .col-md-12.px-md-5
        .row.justify-content-center.mb-5
          #t_2_2.titulo-segundo
            .h2 2.2 Desinfeccin segn patologas
          
          p.mb-5 En caso tal que exista una
            b  patologa asociada a la causa de muerte 
            b-none es importante que 
            b(style="color: #008F85;") el tanatopractor 
            b-none la conozca antes de realizar el
            b(style="color: #008F85;")  proceso de embalsamamiento. 
            b-none  Existen casos en los que algunos individuos mueren a causa de accidentes, homicidios y suicidios; pero en algunos casos no ser as y 
            b la muerte puede ser producto de una 
            b(style="color: #008F85;") enfermedad infectocontagiosa.
          
          .row.justify-content-center.mb-4
            .col-10.col-lg-4.mb-4
              figure
                img.mx-auto(src='@/assets/curso/tema2/img27.svg', alt='imagen ambiental', style="width: 330px;")
            .col-lg-8.mb-4.align-self-center
              .cajon.color-secundario.p-4.mb-0
                p.mb-0 Por eso se sugiere que siempre 
                  b  se debe asumir que los cuerpos estn infectados y tomar las precauciones para su tratamiento 
                  b-none  y as prevenir la transmisin de patgenos tanto al embalsamador como a las personas que tendrn contacto despus del embalsamamiento. 
                  b(style="color: #008F85;")  Segn el Ministerio de Salud de Colombia (2018) 
                  b-none  la ocurrencia de las enfermedades infecciosas obedece a una secuencia de interacciones que permiten que 
                  b  el microorganismo infectante se ponga en contacto con una persona susceptible y produzca en ella la infeccin. 
                  b-none  En este sentido, a continuacin, se describen algunas enfermedades y tratamientos por parte del 
                  b(style="color: #008F85;") tanatopractor 
                  b-none en el momento de la desinfeccin del cadver. 
            
          .row.justify-content-center.mb-2
            .col-lg-12
              .tarjeta.tarjeta-fondito.p-4.mb-0
                SlyderA
                  .row.justify-content-center
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Sndrome de Inmunodeficiencia adquirido (SIDA)
                      p.mb-4 Esta enfermedad implica para el tanatopractor una exposicin por la asociacin de este con la sangre y fluidos del cuerpo. Se recomienda el uso de guantes de nitrilo, mascarilla, cubre ojos y especialmente realizar un proceso de desinfeccin adecuada tanto del cadver como de los elementos e insumos utilizados durante el procedimiento de embalsamamiento, siguiendo las indicaciones del manual del fabricante. 
                      .cajon-b.color-primario.abajo-derecha.p-0.mb-2
                        p.mb-0 El virus del SIDA es frgil y puede ser controlado fcilmente, este se activar luego de un tratamiento de 10 minutos con hipoclorito de sodio al 10 %, 50 % de alcohol etlico, 35 % de alcohol isoproplico, 1 % de surfactante no inico, 0.5 % de lysol, 0.5 % de paraformaldehdo o 0.3 % perxido de hidrgeno. Tambin se podr desactivar el virus por el uso del calor a una temperatura mayor de 56 grados por 10 minutos. 
                  
                    .col-md-5.align-self-center
                      figure.mb-2
                          img.mx-auto(src='@/assets/curso/tema2/img28.png', alt='Realizar adecuada desinfeccin', style='width:450px;')
                        
                  
                  .row.justify-content-center
                    .col-md-5.align-self-center
                      figure.mb-2
                          img.mx-auto(src='@/assets/curso/tema2/img29.png', alt='Inflamacin en el hgado', style='width:450px;')
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Hepatitis
                      .cajon-b.color-primario.p-0.mb-4
                        p.mb-0 Describe aquellos casos de inflamacin en el hgado causada por al menos tres agentes distintos referidos como virus de hepatitis, estos son: hepatitis A, hepatitis B y no virus de hepatitis A o B. 
                      p.mb-2 Este es un virus de importancia especial en la labor de tanatopractor, ya que se puede estar expuesto al tratar el cuerpo y se tiene un gran posibilidad de adquirir la enfermedad si no se encuentra con el esquema de vacunacin completo, ya que la mayora de los casos no son diagnosticados. Para la descontaminacin de la sala de embalsamamiento se sugieren los procesos de esterilizacin y los desinfectantes hechos con el 2% de glutaraldehdo, el instrumental deber limpiarse antes del proceso de desinfeccin garantizando que no tenga material biolgico, ya que este puede ser un obstculo para lograr la desinfeccin adecuada. 
                  
                        
                  .row.justify-content-center
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Tuberculosis
                      p.mb-4 El bacilo de la tuberculosis slo puede ser transmitido de dos formas, por contacto directo como la inhalacin o contacto a travs de la ingestin. 
                      .cajon-b.color-primario.abajo-derecha.p-0.mb-2
                        p.mb-0 Esta enfermedad es particularmente resistente a los desinfectantes especialmente cuando hay presencia de materia orgnica como lo son el esputo y otros fluidos del tracto respiratorio. Por esto representan un riesgo alto de exposicin. Su inactivacin en instrumental se puede realizar por medio de calor, usando autoclaves donde la temperatura sea mayor a 60-70 grados celsius, se recomienda realizar una desinfeccin inicial exhaustiva al cadver con un desinfectante biocida como lo puede ser el hipoclorito de sodio o el glutaraldehdo, pero recordemos que el hipoclorito de sodio es altamente corrosivo por lo cual puede daar fcilmente los elementos del laboratorio.
                  
                    .col-md-5.align-self-center
                      figure.mb-2
                          img.mx-auto(src='@/assets/curso/tema2/img30.png', alt='Transmisin por inhalacin', style='width:450px;')
                        
                  .row.justify-content-center
                    .col-md-5.align-self-center
                      figure.mb-2
                          img.mx-auto(src='@/assets/curso/tema2/img31.png', alt='Inflamacin patolgica cerebral', style='width:450px;')
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Meningitis
                      .cajon-b.color-primario.p-0.mb-4
                        p.mb-0 La palabra meningitis se refiere a la inflamacin patolgica del cerebro o las meninges de cordn espinal. 
                      p.mb-2 Para evitar el contagio de esta enfermedad el tanatopractor debe evitar el contacto directo con las secreciones nasofarngeas, sangre, saliva y contenido de los pulmones del cadver,  para culminar se deber realizar la desinfeccin trmica, especialmente de todos los instrumentos adecuadamente, que ayudar a minimizar la exposicin del riesgo. La higiene personal durante la preservacin del cuerpo ser fundamental para controlar esta enfermedad y las dems infecciones. 
                      
                  .row.justify-content-center
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Septicemia
                      .cajon-b.color-primario.abajo-derecha.p-0.mb-2
                        p.mb-0 El trmino septicemia implica la presencia de bacterias en la sangre, en este sentido es importante la desinfeccin terminal a las superficies que tuvieron contacto con  sangre porque as se reduce la transmisin de las infecciones que pudiesen estar presentes en este lquido corporal. Se sugiere el uso de un fuerte desinfectante. 
                    .col-md-5.align-self-center
                      figure.mb-2
                          img.mx-auto(src='@/assets/curso/tema2/img32.png', alt='Bacterias en la sangre', style='width:450px;')
                      
                  .row.justify-content-center
                    .col-md-5.align-self-center
                      figure.mb-2
                          img.mx-auto(src='@/assets/curso/tema2/img33.png', alt='Afecta nervios debajo de la piel', style='width:450px;')
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Herpes
                      .cajon-b.color-primario.p-0.mb-2
                        p.mb-0 El virus del herpes simple es evidente en las secreciones del cuerpo y afecta principalmente a los nervios debajo de la piel.  Si en el momento de la desinfeccin del cadver se evidencia la infeccin del herpes en los orificios de la boca y genitales debern ser tratados especficamente con fenol para minimizar el riesgo de contagio.
                  
                  .row.justify-content-center
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Citomegalovirus
                      .cajon-b.color-primario.abajo-derecha.p-0.mb-2
                        p.mb-0 La infeccin por citomegalovirus es rara pero produce una variedad de manifestaciones que depende de la edad y el sistema inmune del husped al momento de la infeccin, las clulas largas llamadas citomeglicas y las responsables de producir la enfermedad suele afectar principalmente lo pulmones, hgado y el rin.  Este virus se inactiva fcilmente en solventes lpidos como el ter y el cloroformo a una temperatura de 60 grados celsius por 30 minutos. 
                    .col-md-5.align-self-center
                      figure.mb-2
                          img.mx-auto(src='@/assets/curso/tema2/img34.png', alt='Variedad de manifestaciones', style='width:450px;')
                  
                  .row.justify-content-center
                    .col-md-5.align-self-center
                      figure.mb-2
                        img.mx-auto(src='@/assets/curso/tema2/img35.png', alt='Afecta el sistema nervioso central', style='width:450px;')
                    .col-md-6.mb-4.mb-md-0.align-self-center
                      h3.mb-4 Enfermedad de Creutzfeldt-Jakob (ECJ)
                      .cajon-b.color-primario.p-0.mb-2
                        p.mb-0 Es una enfermedad viral producida por pequeos virus que afectan el sistema nervioso central provocando una disminucin del movimiento corporal y prdida de la funcin mental.transmitirse por el consumo de carne infectada con la variante ECJ, que se cree es la causa de la infeccin de enfermedad de las vacas locas. Los tanatopractores deben tener especial cuidado con este virus ya que no se destruye con el uso del formaldehido y solo podr inactivarse con hipoclorito de sodio.
                      

    p.mb-4 
      b Cuando se inicie embalsamamiento,
      b-none  considerando la condicin infecciosa del cadver se necesitar 
      b respetar las condiciones de cada cuerpo y ofrecer el cuidado adecuado para cada caso. 
      b(style="color: #008F85;")  Es importante recordar 
      b-none que el nmero y la virulencia de los microorganismos en la sala de preparacin pueden ser reducidos con la aplicacin de buenas tcnicas de desinfeccin, rompiendo la cadena propagacin y disminuyendo el riesgo de contagio.
    
    .row.justify-content-center.fondo-bg4
